@media only screen and (min-width: 1024px) {
  .App {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    opacity: 0.7;
  }
  
  .my{
    width: 90vw;
    opacity: 0.7;
  }
  
  .centertext{
    position: absolute;
    top:50%;
    left: 6%;
    font: Garamound;
    font-size: 150px;
  }
  
  .text{
    position: absolute;
    top:90%;
    left: 6%;
    font: Garamound;
    font-size: 50px;
  
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }  
}

@media only screen and (max-width: 1024px) {
  .my{
    width: 100vw;
    height: 100vh;
    opacity: 0.3;
  }
  
  .centertext{
    position: absolute;
    top:50%;
    left: 6%;
    font: Garamound;
    font-size: 40px;
  }
  
  .text{
    position: absolute;
    top:65%;
    left: 6%;
    font: Garamound;
    font-size: 20px;
  
  }

}